export default {
	props: {
		list: {
			type: Array,
			default: () => []
		},
		value: ''
	},
	data() {
		return {
			selectshow: false
		};
	},
	computed: {
		listdata() {
			return this.list;
		}
	},
	mounted() {
		document.addEventListener('click', this.clickOther, true);
	},
	beforeDestroy() {
		this.removeListener();
	},
	methods: {
		removeListener() {
			document.removeEventListener('click', this.clickOther, true);
		},
		clickOther(e) {
			if (this.selectshow) {
				let path = e.path;
				let pathClassNameList = path
					.map(item => item.className)
					.filter(item => !!item);
				let className = this.$el.className;
				if (!pathClassNameList.includes(className)) {
					this.handleBlur();
				}
			}
		},
		handleBlur() {
			// console.log('handleBlur');
			this.selectshow = false;
		},
		selectClick() {
			this.selectshow = !this.selectshow;
		},
		selectName(value) {
			this.$emit('emitValue', value);
			this.selectshow = false;
		}
	}
};
